  height:100%
}
.btnBox{
  position: absolute;right:10px;top:10px;
}
.modelBox{
  background: #F5F5F5;
  height:100%;
  margin-right: -1px;
  p{
    padding:0 15px 0 0;
    position: relative;
    overflow: hidden;
    text-align: left;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height:0;
    }
    strong{
      float: left;
      display: inline-block;
      cursor: pointer;
      height: 100%;
      font-size:14px;
      padding:5px 14px;
    }
    .active{
      border: 1px solid #DADCE0;
      background: #fff;
      border-bottom: 0;
    }
    span{
      float: right;
      cursor: pointer;
    }
  }
  .table_box{
    padding: 0;
    height: calc(100% - 34px);
    background: #fff;
    .thePage{
      position: absolute;
      bottom: 0;
      right: 0;
      width: 100%;
      text-align: right;
      background: #fff;
      padding: 5px 0;
      border: 1px solid #e8eaec;
    }
  }
}
.exportBtn {
  background: #2d579b;
  border-color: #2d579b;
  &:hover {
    background: #1a3a6f;
    border-color: #1a3a6f;
  }
}
// 统计
.statisticForm {
  width: 100%;
  display: flex;
  padding: 10px 0;
  .ivu-form-item {
    margin-bottom: 0;
  }
}
.statisticBox {
  width: 100%;
  height: calc(100% - 53px);
}
// 红线台账登记表
.extract_box{
  height: 100%;
  width: 101%;
  position: absolute;
  right: -120%;
  bottom:0;
  z-index: 1356;
  background: #fff;
  transition: all 0.8s ease;
  border-radius: 0 0 5px 5px;
  .closeBtn{
    position: absolute;
    right:10px;
    bottom:10px;
    z-index: 131531313;
  }
}
.regActive{
  top: 0;
  left: 0;
}
