


























































































































h2{
  padding:5px 0 0;
  font-weight: normal
}
#tab{
  border-collapse: collapse;
  table-layout: fixed;
  text-align: center;
  width: calc(100% - 16px);
  margin: 8px;
  border:solid #d1d1d1; border-width:1px 0 0 1px;
  td{
    padding: 10px 5px;
    font-size: 12px;
    font-weight: bolder;
    border:solid #d1d1d1;
    border-width:0 1px 1px 0;
  }
}
